.container_orders_item_wrapper {
    text-decoration: none;
    color: #fff; }
.container_orders_item {
    width: 99%;
    box-sizing: border-box;
    cursor: pointer;
    background: #1C1C21;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06), 0px 0px 1px rgba(0, 0, 0, 0.04);
    border-radius: 40px; }
.container_orders_item_total {
    display: flex;
    align-items: center; }
.container_orders_item_footer {
    display: flex; }
.container_orders_item_ingredients {
    flex-grow: 1;
    display: flex; }
.container_orders_item_ingredient {
    width: 64px;
    position: relative;
    height: 64px;
    background: #131316;
    border-radius: 100%;
    border: 2px solid #801AB2;
    overflow: hidden;
    img {
        position: absolute;
        width: 112px;
        left: -23px;
        top: 3px; } }
.container_orders_item_ingredient_length {
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, .6);
    z-index: 1;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center; }
.order_header {
    display: flex;
    justify-content: space-between; }
